@import url('https://fonts.googleapis.com/css2?family=Readex+Pro&display=swap'); 



  @import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital@1&display=swap');




* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Readex Pro', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 1.5rem;
}

pl {
  font-size: 2.55rem;
}

pw{
  font-size: 28px;
}

h1{
  font-size: 4.75rem;
}

.app{
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0,0,0,0.4);
  color: #e5a5a5;
}


.app:before {
  content: '';
 background: url('C:/Users/Manamrit/Projects/devsoc-weather-app/src/assets/Aestheticshiz.jpg') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.container{
  max-width: 700px;
  height: 520px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.app .top{
  width: 100%;
  margin: 1rem auto;


}

.app .description{
  position: relative;
  margin-top: -31.5px;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(268.6deg);
}

.app .bottom{
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255,255,255,0.22);
}

.bold{
  font-weight: 700;
}


.app .search{
  text-align: center;
  padding: 1rem;
}

.app .location{
  padding-left: 0.25rem;
}

.app input{
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border:1px solid rgba(255,255,255,0.09);
  background: rgba(255,255,255,0.1);
  color: #f8f8f8;
}

::placeholder{
  color: #f8f8f8;
}

.app .finish{
  text-align: center;
  margin-right: 10px;
 
}